.video-reviews {
    background-color: @white-basic;
    text-align: center;
}

.video-reviews__title {
    .title-h2();
    margin-bottom: 24px;

    @media(min-width: @desktop-width) {
      margin-bottom: 32px;
    }
}

.video-reviews__wrapper {
    margin-bottom: 24px;


    @media(min-width: @tablet-width) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: stretch;
        margin-bottom: 0;
    }
}

.video-reviews__column {
  max-width: 414px;
  margin: 0 auto;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    overflow: hidden;

    @media(min-width: @tablet-width)  {
    max-width: calc(50% - 15px);
    margin: 0;
    margin-bottom: 24px;
    }

    @media(min-width: 992px) {
        max-width: calc(100% / 3 - 14px);
    }
 }

.video-reviews__column + .video-reviews__column {
    margin-top: 24px;

    @media(min-width: @tablet-width) {
        margin-top: 0;
    }
  }


.video-reviews__youtube {
    position: relative;
    max-width: 414px;
    min-height: 207px;
    margin: 0 auto;
    margin-bottom: 8px;
    background-position: center;
    background-size: cover;
    border-radius: 10px;

    @media(min-width: @tablet-width) {
      margin: 0;
      margin-bottom: 8px;
    }
  }

  .video-reviews iframe {
    display: block;
    margin: 0 auto;
    margin-bottom: 20px;
    background-color: @black-basic;
  }

  .video-reviews__head {
    margin: 0;
    color: #ff5a00;
    font-size: 18px;
    font-weight: 400;
  }


  .video-reviews__desc {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 72px;
    max-width: 414px;
    margin: 0 auto;

    @media(min-width: @tablet-width) {
    max-width: max-content;
    margin: 0;
    }
  }

  .video-reviews__name  {
    font-size: 14px;
    line-height: 18px;
    font-weight: 500;
    margin: 0;
  }

  .video-reviews__info {
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    margin: 0;

    span {
        color: @decor-orange;
    }
}

.video-reviews__info + .video-reviews__info {
    margin-top: 4px;
}

  .video-reviews__play {
    position: absolute;
    top: 14px;
    left: 14px;
    width: 32px;
    height: 22px;
    background: url("../img/icons/youtube-1.svg");
    background-repeat: no-repeat;
    border: none;
    background-size: inherit;
    cursor: pointer;
  }

  .video-reviews__button {
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    color: @decor-blue;
    display: block;
    width: 100%;
    margin: 0 auto;
    transition: all 0.7s ease-out;

    &:hover {
      transform: scale(1.33);
      transition: all 0.7s ease-out;
    }

    &:active {
      opacity: 0.6;
    }
  }